.projects-page-container ul.taxonomy-pills {
  padding-left: 1px;
}

.projects-page-container ul.taxonomy-pills li {
  padding: 1.5rem 2rem 1.5rem 0;
  display: inline-block;
}

.projects-page-container ul.taxonomy-pills li:hover {
  text-decoration: underline;
}

.projects-page-container .taxonomy-term-link:focus {
  outline: none;
}

.projects-page-container .taxonomy-pills .taxonomy-term-link {
  text-decoration: none;
}

.projects-page-container .taxonomy-term-link.active {
  color: #000;
  font-weight: bold;
  text-decoration: underline;
}

.projects-page-container .projects-container {
  opacity: 1;
  grid-template-columns: 1fr 1fr 1fr;
  transition: opacity .5s ease-in-out;
  display: grid;
}

.projects-page-container .projects-container.fade-out {
  opacity: 0;
}

.projects-page-container .projects-container.fade-in {
  opacity: 1;
}

.projects-page-container .project-tile-container {
  display: inline-block;
  position: relative;
}

.projects-page-container .project-tile-container .overlay {
  color: #fff;
  box-sizing: border-box;
  background: #00000080;
  width: 100%;
  padding: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.projects-page-container .project-tile-container .overlay h2 {
  margin: 0;
  font-size: 18px;
  line-height: 1.4;
}

.projects-page-container .project-tile-container img {
  width: 100%;
  height: auto;
  display: block;
}

h1 {
  font-size: 20px;
}
